import Tumult from 'tumult';

import Engine from './utils/engine';
// import Engine from './utils/engine3d';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const noise = new Tumult.Perlin3();
const noiseGen = (x, y, z = 1) => {
  let gen = noise.gen(x, y, z);
  return (gen + 1) / 2
};

const cols = 40;
const rows = 40;
const layers = 15;
const layerDepth = 20;

let xOffset = 0;
let yOffset = 100;
let zOffset = 0;

let threshold = 0.5;
let hue = 150;

function drawShape(x, y, width, height) {
  ctx.save();
  // ctx.translate(x + (width / 2), y + (height / 2));
  // ctx.beginPath();
  // ctx.arc(0, 0, width / 2, 0, Math.PI * 2);
  // ctx.fill();
  ctx.translate(x, y);
  ctx.fillRect(0, 0, width, height);
  ctx.restore();
}

function drawPlane(x, y, z, width, height) {
  let xSize = width / cols;
  let ySize = height / rows;

  for (let x = 0; x < cols; x++) {
    for (let y = 0; y < rows; y++) {
      let point = noiseGen((x / 10), (y / 10), z);

      if (point > threshold) {
        drawShape(x * xSize, y * ySize, xSize + 1, ySize + 1);
      }
    }
  }

  ctx.strokeRect(0, 0, width, height);
}

const radians = angle => {
  return degrees * (Math.PI / 180);
}

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);



  ctx.save();
  ctx.transform(0.5, -0.1, 0, 0.5, width / 2, width / 2);

  ctx.translate(
    -((width / 2) + ((layers * layerDepth) / 2)),
    -((height / 2) + ((layers * layerDepth) / 2)),
  );

  for (let i = 0; i < layers; i++) {
    ctx.translate(layerDepth, layerDepth);
    ctx.fillStyle = `hsl(${hue}, ${(((i) / layers) * 100) + 0}%, 50%)`
    ctx.strokeStyle = `hsl(${hue}, 100%, 50%)`
    drawPlane(0, 0, (i / layers) + zOffset, width, height);
  }

  xOffset += 0.01;
  yOffset += 0.01;
  zOffset += 0.01;

  ctx.restore();
});

app.start();
